.main{
  width: 100%;
  height: 100vh;
  padding: 0;
}
.grid{
  display: grid;
  grid-template-columns: 60% 40%;
}
.slides{
  width: 100%;
  height:100vh;
  
 
}
.img{
  width: 100%;
  height: 50rem;
  padding: 5rem;
  padding-top: 10rem;
}
.slides2{
  width: 100%;
  height:100vh;
  padding: 2rem;
  padding-top: 10rem;
  color: white;
 
}
.btn{
  display: block;
  width: 100%;
  padding: 1rem;
  overflow: hidden;
  margin-bottom: 1rem;
  text-decoration: none;
  text-transform: uppercase;
  color: white;
  font-size: 20px;
  text-align: center;
  border-radius: 10px;
  box-shadow: 0px 1px 2px -1px rgba(0,0,0,0.02),
  0px 2px 4px -2px rgba(0,0,0,0.08),
  0px 4px 8px -4px rgba(0,0,0,0.10),
  0px 8px 16px -8px rgba(0,0,0,0.12);
}
.btn:hover{
  text-decoration: none;
  color: white;
}
.grids{
  display: grid;
  grid-template-columns: 40% 40%;
  grid-gap:30px ;  
}
.light{
  background: linear-gradient(180deg, rgba(17, 161, 88, 1), rgba(0, 141, 68, 1));
}
.dark{
  background: linear-gradient(180deg, rgba(50, 50, 50, 1), rgba(0, 0, 0, 1));
}


.igrid{
  display: grid;
  grid-template-columns: 45% 45%;
  grid-gap: 30px;
}
.img{
    width: 100%;
    height: 50rem;
    padding-top: 10rem;
}


.inputGroup {
    display: block;
    width: 100%;
    max-width: 100%;
    height: 82px;
    border: 1px solid  rgba(17, 161, 88, 1);
    background-color: #ffffff;
    border-bottom-left-radius: 41px;
    border-bottom-right-radius: 41px;
    border-top-left-radius: 41px;
    border-top-right-radius: 0;
    box-shadow: 0 17px 40px 0 rgba(75, 128, 182, 0.07);
    margin-bottom: 22px;
    position: relative;
    font-size: 17px;
    color: #a7b4c1;
    transition: opacity 0.2s ease-in-out, filter 0.2s ease-in-out,
      box-shadow 0.1s ease-in-out;
  }
  
  .inputGroup:hover {
    box-shadow: 0 14px 44px 0 rgba(0, 0, 0, 0.077);
  }
  
  .inputGroup input {
    position: absolute;
    border: 0;
    box-shadow: none;
    background-color: rgba(255, 255, 255, 0);
    top: 0;
    height: 65px;
    width: 100%;
    padding: 0 53px;
    box-sizing: border-box;
    z-index: 3;
    display: block;
    color: #1a6fc4;
    font-size: 17px;
    font-family: "Oxygen", sans-serif;
    transition: top 0.1s ease-in-out;
  }
  
  .inputGroup input::placeholder {
    color: rgba(0, 0, 0, 0);
  }
  
  .inputGroup input:focus,
  .inputGroup input:not(:placeholder-shown) {
    top: 17px;
    outline: none;
  }
  
  .inputGroup label {
    position: absolute;
    border: 0;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 2;
    display: flex;
    align-items: center;
    padding: 0 53px;
    box-sizing: border-box;
    transition: all 0.1s ease-in-out;
    cursor: text;
    color:  rgba(17, 161, 88, 1);
  }
  
  .inputGroup input:focus + label,
  .inputGroup input:not(:placeholder-shown) + label {
    bottom: 20px;
    font-size: 13px;
    opacity: 0.7;
 
  }

.btn:hover{
    text-decoration: none;
    color: white;
}



