.main{
    width: 100%;
    height: 100vh;
    padding: 0;
}
.grid{
    display: grid;
    grid-template-columns: 60% 40%;
}
.slides{
    width: 100%;
    height:100vh;
    
   
}
.img{
    width: 100%;
    height: 30rem;
    padding: 5rem;
    padding-top: 10rem;
}
.slides2{
    width: 100%;
    height:100vh;
    padding: 2rem;
    padding-top: 10rem;
    color: white;
   
}
.btn{
    display: block;
    width: 100%;
    padding: 1rem;
    overflow: hidden;
    margin-bottom: 1rem;
    text-decoration: none;
    text-transform: uppercase;
    color: white;
    font-size: 20px;
    text-align: center;
    border-radius: 10px;
    box-shadow: 0px 1px 2px -1px rgba(0,0,0,0.02),
    0px 2px 4px -2px rgba(0,0,0,0.08),
    0px 4px 8px -4px rgba(0,0,0,0.10),
    0px 8px 16px -8px rgba(0,0,0,0.12);
}
.btn:hover{
    text-decoration: none;
    color: white;
}
.grids{
    display: grid;
    grid-template-columns: 40% 40%;
    grid-gap:30px ;  
}
.light{
    background: linear-gradient(180deg, rgba(17, 161, 88, 1), rgba(0, 141, 68, 1));
}
.dark{
    background: linear-gradient(180deg, rgba(50, 50, 50, 1), rgba(0, 0, 0, 1));
}



